@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Oswald:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2 {
  margin: 0.5rem 0 1rem;
}

.app {
  height: 100vh;
}

.box {
  margin: 1rem;
  padding: 0.5rem;
  background-color: #f5f5f5;
}

.card {
  padding: 0.5rem 1rem;
}

.profile-box h2 {
  color: #4d4d4d;
  font-size: 0.8rem;
  text-transform: uppercase;
}

.profile-box p {
  font-size: 0.7rem;
  margin-top: 1rem;
  color: #808080;
}

.profile-card {
  background-color: #fff;
  padding: 1rem;
  border-radius: 0.4rem;
}

.profile .fa-react {
  font-size: 6rem;
  border-radius: 50%;
  background-color: black;
}

.profile {
  margin-bottom: 1.5rem;
}

.profile .fa-check-circle {
  color: blueviolet;
}

.skill-item {
  font-family: monospace;
  font-size: 0.8rem;
  background-color: blueviolet;
  color: #f2f2f2;
  border-radius: 5px;
  display: inline-block;
  padding: 3px 5px;
  margin: 5px;
  text-align:center
}
.icons-box h1 {
  font-size: 1.3rem;
  text-align: center;
  margin: 0 0 1rem;
}

.icons-box h1 {
  font-size: 1.3rem;
  text-align: center;
  margin: 0 0 1rem;
  color: #4d4d4d;
}

.icons {
  display: flex;
  justify-content: space-around;
}

.icons .svg-inline--fa {
  font-size: 6rem;
}

.fa-html5 {
  color: orange;
}

.fa-css3-alt {
  color: steelblue;
}

.fa-js {
  color: gold;
}

.fa-react {
  color: lightskyblue;
}
.signup-form {
  font-family: "Open Sans", sans-serif;
}

::-webkit-input-placeholder {
  font-size: 0.7rem;
  color: #ccd3d8;
}

:-ms-input-placeholder {
  font-size: 0.7rem;
  color: #ccd3d8;
}

::-ms-input-placeholder {
  font-size: 0.7rem;
  color: #ccd3d8;
}

::placeholder {
  font-size: 0.7rem;
  color: #ccd3d8;
}

.signup-form input,
.signup-form button {
  border-radius: 0.4rem;
  padding: 0.3rem;
  border: 1px solid #c2e6f4;
}

.signup-form input {
  width: 10rem;
  margin: 1rem 0.3rem;
}

.signup-form button {
  font-size: 0.7rem;
  color: #f8f3f4;
  background-color: #f37474;
  width: 15rem;
  padding: 0.5rem;
}

.signup-content {
  background-color: #c2e6f4;
  padding: 1rem;
  border-radius: 0.8rem;
}

.signup-box {
  padding: 1rem 1rem;
  text-align: center;
}

.signup-box h2 {
  font-family: Oswald, sans-serif;
  font-size: 1.2rem;
  text-transform: uppercase;
}

.signup-box p {
  font-size: 0.8rem;
}

.color-boxes {
  text-align: center;
  font-weight: bold;
  color: snow;
}

.color-box {
  padding: 1rem;
  height: auto;
}

